// This code calculates infection risk and CO2 concentration in a room using the Wells-Riley model and the CO2 balance equation, respectively.

function calculateInfectionRisk(volume, exposureTime, positiveRate, persons, breathingFlowRate, quantaGenerationRate, airChangesHourly, maskRate, maskEfficacy) {
  // Calculate the infection risk using the Wells-Riley model

  const infectedPersons = Math.max(1, positiveRate * persons);
  const susceptiblePersons = persons - infectedPersons;
  const ventilationRate = volume * airChangesHourly;
  const mask = 1-(maskRate * maskEfficacy);
  const emissionRate = (infectedPersons * quantaGenerationRate * mask) * mask

  const tauVF = 1 / airChangesHourly
  const tauD = 0.87;
  const tau = 1 / (1/tauVF + 1/tauD) // (tau = 1 / (1/tauVF + 1/tauD + 1/tauF))
  const concentrationDose = emissionRate * tau / volume
  const risk = 1 - Math.exp(-1 * concentrationDose * breathingFlowRate * exposureTime)
  const probability = risk;
  const odds = probability / (1 - probability);

  return {
    'Infection Risk': Math.min(99.9, risk * 100),
    'Probability of Infection': probability,
    'Odds of Infection': odds,
  };
}



function calculateInfectionRiskOld(volume, exposureTime, positiveRate, persons, breathingFlowRate, quantaGenerationRate, airChangesHourly) {
  // Calculate the infection risk using the Wells-Riley model

  const infectedPersons = positiveRate * persons;
  const susceptiblePersons = persons - infectedPersons;
  const ventilationRate = volume * airChangesHourly;
  const iqpt = infectedPersons * breathingFlowRate * quantaGenerationRate * exposureTime;
  const risk = 1 - Math.exp(-1 * iqpt / ventilationRate);
  const probability = risk;
  const odds = probability / (1 - probability);

  return {
  'Infection Risk': risk * 100,
  'Probability of Infection': probability,
  'Odds of Infection': odds,
  };
}

function calculateInfectionRiskD(volume, exposureTime, positiveRate, persons, breathingFlowRate, quantaGenerationRate, airChangesHourly, halfLife) {
  const infectedPersons = positiveRate * persons;
  const susceptiblePersons = persons - infectedPersons;
  const ventilationRate = volume * airChangesHourly;
  const timeSinceEmission = exposureTime + (1 / airChangesHourly) * Math.log(2) * halfLife;
  const qa = infectedPersons * quantaGenerationRate * Math.exp(-(Math.log(2) * timeSinceEmission) / halfLife);
  const ci = qa / volume + (infectedPersons / susceptiblePersons) * (qa / volume - quantaGenerationRate / breathingFlowRate);

  const risk = 1 - Math.exp(-1 * ci * breathingFlowRate * susceptiblePersons * exposureTime / ventilationRate);
  const probability = risk;
  const odds = probability / (1 - probability);

  return {
    'Infection Risk': risk * 100,
    'Probability of Infection': probability,
    'Odds of Infection': odds,
  };
}

calculateInfectionRiskD(1000, 1, 0.05, 10, 0.5, 100, 2, 1.1)

function calculateCO2(q, n, V, t, ci, c0) {
  // Calculate the CO2 concentration in parts per million (ppm) using the CO2 balance equation
  const e = 2.71828;
  const c = (q / (n * V)) * (1 - (1 / (e ** (n * t)))) + ((c0 - ci) / (e ** (n * t))) + ci;

  // Convert the concentration to parts per million (ppm)
  const ppm = c * 1000000;

  return ppm;
}

function roomCo2(volume, airChangesPerHour, occupancy, activityLevel) {
  // Calculate the CO2 concentration in a room using the CO2 balance equation
  const q = 0.05 * activityLevel; // CO2 generation rate (m3/h/person)
  const V = volume * 0.0283168; // Convert volume from cubic feet to cubic meters
  const n = airChangesPerHour; // Air changes per hour
  const t = 1; // Time in hours
  const ci = 0.0004; // Carbon dioxide concentration in the make-up air (m3/m3)
  const c0 = 420 / 1000000; // Carbon dioxide concentration in the room at start (m3/m3)

  const co2 = calculateCO2(q * occupancy, n, V, t, ci, c0);

  return co2.toFixed(2); // Return CO2 concentration rounded to 2 decimal places
}

// Example inputs
const volume = 10000; // Volume in cubic feet
const airChangesPerHour = 2; // Air changes per hour
const occupancy = 50; // Number of people in the room
const activityLevel = 1; // Activity level of the people in the room

// Calculate CO2 concentration in the room
const co2Concentration = roomCo2(volume, airChangesPerHour, occupancy, activityLevel);



module.exports = { calculateInfectionRisk, calculateCO2, roomCo2 };
