import React, { useState, useEffect } from 'react';
import Room from './Room.js';
import Filter from './Apu.js';
import PriceTag from './PriceTag.js';
import { UserWarning } from './BuildZone.js';

function Warehouse(props) {
  const [currentFactory, setCurrentFactory] = useState(() => {
    const factoryFromStorage = JSON.parse(localStorage.getItem('currentFactory'));
    return factoryFromStorage || props.factory;
  });

  const [isMetric, setIsMetric] = useState(false);

  // Function to update the current factory with a new factory
  const updateFactory = (newFactory) => {
    setCurrentFactory({
      ...currentFactory,
      control: {
        ...currentFactory.control,
        lab: newFactory.control.lab,
      },
    });
  };

  // Function to toggle between metric and imperial units
  const toggleUnits = () => {
    setIsMetric((prevIsMetric) => !prevIsMetric);
  };


  return (
    <div className="Warehouse">
      {props.mode === 0 && (
        <div style={{ margin: '20px' }}>
          <label htmlFor="units-select">Select Units:</label>
          <select id="units-select" onChange={toggleUnits} value={isMetric ? "metric" : "imperial"}>
            <option value="metric">Metric</option>
            <option value="imperial">Imperial</option>
          </select>
        </div>


      )}
      <div className="Production-line">
        <div className="Part"></div>
        {props.mode === 1 && (
          <div className="Part">
            {/* Filter component for selecting an air purifier */}
            <Filter mode={props.mode} factory={currentFactory} onFactoryChange={updateFactory} />
          </div>
        )}
        {(props.mode === 1 || props.mode === 2) && (
          <div className="Part">
            {/* Room component for setting the room dimensions */}
            <Room mode={props.mode} lab={currentFactory.control.lab} control={currentFactory.control} factory={currentFactory} updateFactory={updateFactory} />
          </div>
        )}
        {props.mode === 1 && (
          <div className="Part">
            {/* PriceTag component for displaying the cost of the air purifier */}
            <PriceTag factory={currentFactory} onFactoryChange={updateFactory} />
          </div>
        )}
        {/* Button to toggle between metric and imperial units */}
      </div>
    </div>
  );
}

export default Warehouse;
