import React, {useState, useEffect} from 'react';


function FilterSettings(props) {
  const {
    powered,
    togglePower,
    toggleSelection,
    name,
    updateName,
    cadr,
    updateCadr,
    watts,
    updateWatts,
    price,
    img,
    updatePrice,
    update,
    apu,
    updateImage
  } = props;

  const handleFilterSelect = (event) => {
    const index = apu.catalogue.findIndex((item) => item.Model === event.target.value);
    apu.selectFilter(index);
    update();
  };

  return (
    <div id="filterSpecs">
      <button className="NewFilter" onClick={props.closeSettings}>
        <span id="filterEdit" className="material-icons">
          close
        </span>
        <p>Close</p>
      </button>
      <h2>Filter Stats</h2>
      <h2 id="filterTitle">
        <select className="NewFilter" value={name} onChange={handleFilterSelect}>
          {apu.catalogue.map((item) => (
            <option key={item.Model} value={item.Model}>
              {item.Model}
            </option>
          ))}
        </select>
      </h2>
      <label className="specLabel" htmlFor="filterUnitName">
        Name
      </label>
      <br />
      <input
        className="filterSpec"
        id="filterUnitName"
        type="text"
        name={name}
        value={name}
        onChange={updateName}
      />
      <br />
      <br />
      <label className="specLabel" htmlFor="filterUnitCadr">
        Delivery Rate (ft³/min)
      </label>
      <br />
      <input className="filterSpec" id="filterUnitCadr" type="number" onChange={updateCadr} value={cadr} />
      <br />
      <br />
      <label className="specLabel" htmlFor="filterUnitWatts">
        Watts
      </label>
      <br />
      <input className="filterSpec" id="filterUnitWatts" type="number" onChange={updateWatts} value={watts} />
      <br />
      <br />
      <label className="specLabel" htmlFor="filterUnitPrice">
        Unit Cost
      </label>
      <br />
      <input className="filterSpec" id="filterUnitPrice" type="number" onChange={updatePrice} value={price} />
      <br />
      <br />
      <label className="specLabel">Image</label>
      <br />
      <input className="filterSpec" id="filterImage" type="text" value={img} onChange={updateImage} />
      <br />
      <br />
    </div>
  );
}


function FilterUnits({isrc, units}) {
  return (
    <div className='FilterUnits'>
      <div className='APU'>
        <div className='APUpic'>
          <img id='roomImg' src={isrc}/>
        </div>
        <h2 id='x2'>x</h2>
        <div className='fUnit'>
          <p id='nFilters'>{Math.ceil(units)}</p>
        </div>
      </div>
    </div>
  );
}

export default function Filter({ mode, factory, onFactoryChange }) {
  // lab is
  const lab = factory.control.lab;

  const roomVals = lab.getRoomValues();

  const vals = lab.getFilterValues();
  lab.room.costEstimate();

  const f_ = lab.room.filters[lab.room.selectedFilter];
  const [apu, setApu] = useState(f_);
  const [powered, setPowered] = useState(apu.on);
  const [name, setName] = useState(apu.name);
  const [watts, setWatts] = useState(apu.watts);
  const [price, setPrice] = useState(apu.price);
  const [cadr, setCadr] = useState(apu.cadrValues);
  const [src, setSrc] = useState(vals['Source']);
  const [bM, setBuildMode] = useState(false);
  const [isrc, setIsrc] = useState(apu.image);
  const [buy, setBuy] = useState(apu.buy);
  const [units, setUnits] = useState(apu.units);
  const [filterSelection, setfilterSelection] = useState("select-filter-dropdown-content");
  const estimate = `An estimated ${apu.units} units to achieve ${roomVals['ACH Target']} air changes per hour!`;

  const handleApuChange = () => {
    const updatedFactory = {
      ...factory,
      control: {
        ...factory.control,
        lab: {
          ...factory.control.lab,

        }
      }
    };
    onFactoryChange(updatedFactory);
  };
  const handleApuFieldChange = (field, value) => {
    apu[field] = value;
    switch (field) {
      case "price": setPrice(value); break;
      case "watts": setWatts(value); break;
      case "cadrValues":
        setCadr(value);
        lab.setFilterValues(apu);
        apu.units = lab.getRoomValues()['Filters'];
        break;
      case "name": setName(value); break;
      case "image":
        setIsrc(value);
        break;
      case "on": setPowered(value); break;
      default: break;
    }
    lab.setFilterValues(apu);
    const updatedFactory = {
      ...factory,
      control: {
        ...factory.control,
        lab: {
          ...factory.control.lab,

        }
      }
    };
    onFactoryChange(updatedFactory);
  };

  const updateUnitPrice = event => {
    handleApuFieldChange("price", event.target.value);
  };

  const updateImg = event => {
    handleApuFieldChange("image", event.target.value);
  };

  const updateWatts = event => {
    handleApuFieldChange("watts", parseFloat(event.target.value));
  };

  const updateCadr = event => {
    const cadrValue = parseFloat(event.target.value);
    handleApuFieldChange("cadrValues", [cadrValue, 20]);
  };

  const updateName = event => {
    handleApuFieldChange("name", event.target.value);
  };

  const updatePower = event => {
    handleApuFieldChange("on", !apu.on);
  };

  const updateAll = event => {
    const { price, watts, cadrValues, name, on, image } = apu;
    lab.setFilterValues(apu);
    setPrice(price);
    setWatts(watts);
    setCadr(cadrValues);
    setName(name);
    setPowered(on);
    setIsrc(image);

    const roomVals = lab.getRoomValues();
    const nFilters = Math.round(parseFloat(roomVals['Filters']));
    document.getElementById('nFilters').innerHTML = nFilters;

    const updatedFactory = {
      ...factory,
      control: {
        ...factory.control,
        lab: lab,
      },
    };
    onFactoryChange(updatedFactory);
  };

  const selectFilter = event =>{
  //  let unitInfo = lab.bridge.getLightByName(event);

    setName(event);
  }
  // cost estimate perform inside vapor labs for simpler functions
  const toggleSelection = event =>{
    if (filterSelection === "select-filter-dropdown-content") {
      setfilterSelection("select-filter-dropdown-content-show");
    } else {
      setfilterSelection("select-filter-dropdown-content");
    }
  }
  const updateMode = event =>{
    if (mode === 1) {
      apu.units = 1;
      setUnits(apu.units);
    }
  }
  const handleFilterSelect = (event) => {
    const index = apu.catalogue.findIndex((item) => item.Model === event.target.value);
    apu.selectFilter(index);
    updateAll();
  };
  return (
    <div className='Filter' onChange={() => {updateMode(0); updateAll();}} >
      {
        bM?
        <FilterSettings
          powered={powered}
          togglePower={updatePower}
          toggleSelection={toggleSelection}
          name={name}
          updateName={updateName}
          cadr={cadr[0]}
          updateCadr={updateCadr}
          watts={apu.watts}
          updateWatts={updateWatts}
          price={apu.price}
          updatePrice={updateUnitPrice}
          updateImage={updateImg}
          closeSettings={() => setBuildMode(!bM)}
          update={updateAll}
          apu={apu}
          onChange={handleApuChange}
          img={isrc}
        />
        :
          <div id='filterName'>
            <button
              className='NewFilter'
              onClick={ () => setBuildMode(!bM) }
            >
              <div id='filterEdit' className="material-icons">edit</div><p>Edit</p>
            </button>
            <h2 id='filterTitle'>{name}</h2>
          </div>
      }
      <div>
        <FilterUnits isrc={isrc} units={apu.units}/>
      </div>
      <div id='filterEstimate'>
        <p id='fEst'>{estimate}</p>
      </div>
      <div id='orderButtons'>
        <a href={buy}>
          <div id='buyButton'>
            <h3>Buy</h3>
          </div>
        </a>
      </div>
    </div>
  );
}
