import React from 'react';
import label from './images/label.png';

function PriceTag({ factory }) {
  const costEstimate = React.useMemo(() => factory.control.lab.room.costEstimate(), [factory]);
  return (
    <div className='Room'>
      <div id='titleHeader'>
        <h2>Price</h2>
      </div>
      <div id='tagContent'>
        <div id='tag'>
          <div id='tagTxt'>
            <p id='priceStatement'>
              {costEstimate.lifetime.statement}
            </p>
            <a href='https://www.cnbc.com/2022/12/01/long-covid-costs-patients-an-average-9000-a-year-in-medical-expenses.html' className='more-link'>More</a>
          </div>
          <img id='limg' src={label} alt="price tag"/>
        </div>
      </div>
      <div id='price'>
        <h4>~${costEstimate.year.cost}</h4>
      </div>
    </div>
  );
}


export default PriceTag;
