import React, { useState, Fragment  } from 'react';
import vwLogo from './images/VW-Logo-Alpha.png';
import vwWire from './images/VW-Logo-Warehouse-Alpha.png';
import vwSun from './images/VW-Logo-Sun-Alpha.png';
import Warehouse from './Warehouse.js';
import { BZone } from './BuildZone.js';
import { FilterFactory } from './VapeLab/js/Builder.js';
import { UserWarning } from './BuildZone.js';
import  Aranet  from './Aranet4Reader.js';

function Nav() {
    const [tool, setTool] = useState('Unit Calculator');
    const [mode, setMode] = useState(1);
    const [showPartInfo, setShowPartInfo] = useState(true);

    function handleToolClick(toolName) {
      const modes = { 'Settings': 0, 'Unit Calculator': 1, 'Risk Estimator': 2, 'APU Programmer': 3 };
      setTool(toolName);
      setMode(modes[toolName]);
    }

    // Create a dynamic heading that displays the current tool name
    const heading = tool === 'Settings' ? 'Settings' : `${tool}`;


    const descriptions = {
      'Settings': 'Customize various settings.',
      'Unit Calculator':  <>
          Calculate the ideal quantity of air changes per hour (ACH) required to maintain good indoor air quality in your indoor environment. Air changes per hour refer to the number of times the air in a room is completely replaced with fresh air in one hour.
          The calculator takes into account the volume of the room, the number of occupants, and their activities, as well as the efficiency of the air purifier. It provides you with the recommended ACH value, the required air flow rate, and the number of air purifiers needed to achieve the desired ACH value.
          Check out this <a href='https://www.epa.gov/indoor-air-quality-iaq/how-much-ventilation-do-i-need-my-home-improve-indoor-air-quality'>link</a> to learn more about air changes per hour and their importance in maintaining good indoor air quality.
          Additionally, you can simulate the performance of various air purifiers and adjust their parameters to see their effect on the recommended ACH value. The calculator provides you with an estimated.
        </>,
      'Risk Estimator': <>
        <p>
          Assess the risk of viral transmission in indoor spaces using the <a href='https://en.wikipedia.org/wiki/Wells-Riley_model'>Wells-Riley model</a>. Originally developed for tuberculosis and measles, it can also be applied to other airborne diseases such as COVID-19.
        </p>
        <p>
          The tool predicts the probability of infection for susceptible individuals in a room with one or more infected individuals. It factors in the size of the room, ventilation, and infectiousness of the individual. The model assumes that infection occurs if a person breathes in one or more doses of the infectious agent, and calculates the probability of this happening.
        </p>
      </>,
      'APU Programmer': 'Program and customize your air purifiers to suit your specific needs.'
    };

    const description = descriptions[tool];

    function hidePartInfo() {
      setShowPartInfo(false);
    }

  return (
    <div className='Menu'>
      <div id='scrollprompt'>
        <h3>scroll down</h3>
        <span className='material-icons'>arrow_downward</span>
      </div>
      <div className='logo'>
        <img id='logoSun' src={vwSun} alt='VW Logo Sun' />
        <img id='logoWire' src={vwWire} alt='VW Logo Warehouse' />
        <h1>Vapor Warehouse</h1>
      </div>
      <h2 id='tooltype'>{heading}</h2>
      {showPartInfo && (
        <div id='partInfo'>
          <i className='material-icons closeButton' onClick={hidePartInfo}>close</i>
          <p>{description}</p>
        </div>
      )}
      <div className='ToolBar'>
        {[
        //  { name: 'Settings', icon: 'settings' },
          { name: 'Unit Calculator', icon: 'calculate' },
          { name: 'Risk Estimator', icon: 'coronavirus' },
      //    { name: 'APU Programmer', icon: 'smart_toy' }
        ].map(toolItem => (
          <Fragment key={toolItem.name}>
            <i
              className={`material-icons ${tool === toolItem.name ? 'selected' : ''}`}
              onClick={() => handleToolClick(toolItem.name)}
            >
              {toolItem.icon}
            </i>
            <h4>{toolItem.name.split(' ')[0]}</h4>
          </Fragment>
        ))}
      </div>
      <Warehouse mode={mode} factory={new FilterFactory()} />
      <UserWarning />
    </div>
  );
}


export default Nav;
