

import data from './APU-Catalogue.json';


const { calculateInfectionRisk, calculateCO2, roomCo2 } = require('./room-safety');



class Room {
  constructor(area, height, ach = 1, minDistance = 6, contagion = 'SARS-2:Alpha', time = 0.5, dailyUsage = 0.33, kWh = 0.12) {
    [this.area, this.height, this.contagion, this.time, this.dailyUsage, this.kWh, this.distancing, this.ach, this.positive, this.maskRate, this.maskEfficacy] = [area, height, contagion, time, dailyUsage, kWh, minDistance, ach, 5, 0, 30];
    this.volume = parseFloat((area * height).toFixed(2));
    this.occupancy = area / 50;
    this.maxOccupants = Math.floor(area / 30);
    this.filterDistance = area / minDistance ** 2;
    this.occupants = parseFloat(this.maxOccupants.toFixed(1));
    this.personCFM = 60;
    this.baseACH = ach;
    this.maxACH = 32;
    this.selectedFilter = 0;
    this.filters = [];
    this.quanta = 200;
    this.getRisk(1);
    this.getFilterInUse();
} getRisk(bACH) {
   const ach = Math.max(this.baseACH, this.ach);
   const { 'Infection Risk': risk } = calculateInfectionRisk(this.volume, this.time, this.positive / 100, this.occupancy, 0.5 * 35.3147, this.quanta, ach, this.maskRate, this.maskEfficacy);
   this.risk = risk.toFixed(1);
   this.co2 = parseInt(roomCo2(this.volume, this.baseACH, this.occupancy, 1));
 } getACH() {
  const filter = this.filters[this.selectedFilter];
  const ach = ((filter.cadr * filter.units * 60) / this.volume) + this.baseACH;
  this.ach = ach.toFixed(2);
  this.getRisk(1);
  return this.ach;
} getUnits(desiredACH = 6, perOccupant = false, unitMax = false) {
  const filter = this.filters[this.selectedFilter];
  let eACH = desiredACH - this.baseACH;
  let dCADR = eACH * this.volume / 60;

  if (perOccupant) {
    const cadrNeed = this.occupants * this.personCFM - this.baseACH * this.volume / 60;
    filter.units = cadrNeed / filter.cadr;
  } else {
    filter.units = dCADR / filter.cadr;
  }

  if (unitMax) {
    filter.units = Math.min(filter.units, this.maxUnits(filter));
  }

  filter.units = parseFloat(filter.units.toFixed(1));
  return filter.units;
} maxUnits(f) {
    // Find the max number of units possible for the room space.
    let m;
    if (f.stackable) {
      // Give at least 1 foot of space above stacked fan.
      m = (this.height - 1) / f.size;
      m = m * this.filterDistance;
    } else {
      m = this.filterDistance;
    }
    return m;
  } costEstimate() {

    function calculateYearlyCost(filter, dailyUseHours, energyRate) {
      // Calculate the annual cost of using a filter based on its power, lifespan, and price.

      const dailyPowerConsumption = filter.power * dailyUseHours;
      const annualEnergyConsumption = dailyPowerConsumption * 365 * filter.units;
      let price = (filter.units * filter.price) / filter.unitLife;

      const unitCostPerYear =  price * ((1 / filter.life)*filter.mediaPrice);
      const energyCostPerYear = (annualEnergyConsumption / 1000) * energyRate;

      return {
        'cost': (unitCostPerYear + energyCostPerYear).toFixed(2),
        'description': 'Yearly Cost [Unit + Energy]',
        'statement': 'Annual cost of filtering your space.'
      };
    }
    function lifetimeComparison(cost) {
      // Compare the cost of filtering the air vs COVID-19 infection.

      // https://www.cnbc.com/2022/12/01/long-covid-costs-patients-an-average-9000-a-year-in-medical-expenses.html
      const ANNUAL_INFECTION_COST = 9000*5; // USD
      const compare = ((cost / ANNUAL_INFECTION_COST) * 100).toFixed(2);
      const save = `Just ${compare}% the five year cost of a single case of Long-COVID.`;
      const link = 'https://www.cnbc.com/2022/12/01/long-covid-costs-patients-an-average-9000-a-year-in-medical-expenses.html';

      return {
        statement: save,
        src: link
      };
    }

    let filter = this.filters[this.selectedFilter];
    filter.setPower(50);
    let estimate = {
      'year':calculateYearlyCost(filter, filter.dailyUsage, this.kWh)
    };
    estimate['lifetime'] = lifetimeComparison(estimate['year']['cost']);
    this.estimate = estimate;
    return estimate;
  } getFilterInUse() {
    this.filterInUse = [this.filters[this.selectedFilter]];
    return this.filterInUse;
  } getMaxACH() {
    let filter = this.filters[this.selectedFilter];
    let cadr = filter.cadr; // Get the CADR for the selected filter

    // Calculate the ACH for the room based on the selected filter
    let totalCADR = cadr * 60; // Convert CADR to CADR/hour
    let ach = totalCADR / this.volume; // Calculate ACH
    this.maxACH = ach + this.baseACH; // Add the base ACH to get the maximum ACH
    this.maxACH = this.maxACH.toFixed(2); // Round off to two decimal places

    return this.maxACH;
  }

}

class APU {
  constructor(cleanAirDeliveryRates, powerDraw, unitPrices) {
    this.catalogue = data;
    this.id = '';
    this.name = 'Generic Air Filter';
    this.image = '';
    this.cadrValues = cleanAirDeliveryRates;
    this.watts = powerDraw;
    this.dailyUsage = 0.33;
    this.units = 1;
    this.life = 0.5; // Lifetime in years, replace every 6 months in default case.
    this.unitLife = 5; // Years the unit lasts
    this.mediaPrice = 20; // replacement filter cost in USD
    this.size = 1.6; // Square feet of space needed per unit. Assume box shape.
    this.stackable = false; // Units can be stacked.
    this.price = unitPrices;
    this.setPower(50); // Start your filters on med-power!
    this.isOn = false;
    let randomIndex = Math.floor(Math.random() * this.catalogue.length); // Select Random Filter to init
    this.selectFilter(randomIndex);
  }
  setPower(power) {
    // Power input levels 0-100
    this.powerLevel = power / 100;
    const p = this.cadrValues[1] * this.powerLevel;

    // CADR based on power input.
    this.cadr = this.cadrValues[0] + p;
    this.power = this.watts;
    return this.cadr;
  }
  selectFilter(index) {
    let newAPU = this.catalogue[index];
    this.name = newAPU['Model'];
    this.image = newAPU['Image'];
    this.cadrValues = [newAPU['CADR (Cubic feet/min)'],20];
    this.watts = newAPU['Watts'];
    this.buy = newAPU["Buy"];
    this.price =  parseFloat(newAPU['Cost NZD'].replace(/[$,]/g, "")) * 0.62;
    return newAPU;
  }

  turnOn(state) {
    this.isOn = state;
    if (this.isOn) {
      console.log('Filter is on.');
    } else {
      console.log('Filter is off.');
    }
  }
}


export {Room, APU};
