import React, { useMemo, useRef, useCallback } from 'react';
import ReactDOM from 'react-dom';
import { Canvas, useFrame } from 'react-three-fiber';
import { OrbitControls } from '@react-three/drei';
import * as THREE from 'three';

const Particle = React.memo(({ position, speed, r }) => {
  const ref = useRef();
  const velocity = useMemo(() => {
    const randomValues = [0.5 + Math.random(), Math.random() - 0.5, Math.random() - 0.5];
    return new THREE.Vector3(...randomValues);
  }, []);

  const onFrame = useCallback(({ clock }) => {
    const t = clock.getElapsedTime() * speed;
    const { position, rotation, material } = ref.current;
    const newPosition = position.clone().add(velocity.clone().multiplyScalar(speed / 100));

    const absNewPosition = newPosition.clone().setY(Math.abs(newPosition.y)).setZ(Math.abs(newPosition.z));
    velocity.setY(Math.abs(newPosition.y) > 1 ? -velocity.y : velocity.y);
    velocity.setZ(Math.abs(newPosition.z) > 1 ? -velocity.z : velocity.z);
    newPosition.setX(newPosition.x > 1 ? -1 : newPosition.x);

    position.copy(newPosition);
    rotation.x = t * 1.5;
    rotation.y = t * 1.5;

    const rClamped = Math.min(r, 90);
    material.color = new THREE.Color(rClamped / 80, 1 - rClamped / 80, 0);
  }, [velocity, speed, r]);

  useFrame(onFrame);

  return (
    <mesh ref={ref} position={[position.x, position.y, position.z]}>
      <sphereBufferGeometry args={[0.1, 16, 16]} />
      <meshBasicMaterial />
    </mesh>
  );
});

const CubeWireframe = () => (
  <group>
    <mesh>
      <boxBufferGeometry args={[2, 2, 2]} />
      <meshBasicMaterial color="#00ff00" wireframe linewidth={0} />
    </mesh>
    <mesh>
      <boxBufferGeometry args={[1.95, 1.95, 1.95]} />
      <meshBasicMaterial color="#00ff00" opacity={0.1} transparent />
    </mesh>
  </group>
);


const RoomWireframe = ({ doses, speeding, risk, volume }) => (
  <div id='volumeBox'>
    <div id='cubicFootcon'>
      <div id='cubicFoot'>
        <Canvas  camera={{ position: [4, 4, 2], up: [0, 0, 1], fov: 50 }}>
          <OrbitControls />
          <CubeWireframe />
          {[...Array(doses)].map((_, i) => (
            <Particle
              key={i}
              position={{
                x: Math.random() * 2 - 1,
                y: Math.random() * 2 - 1,
                z: Math.random() * 2 - 1,
              }}
              speed={speeding + Math.random()}
              r={risk}
            />
          ))}
        </Canvas>
      </div>
    </div>
    <div id='cubicFootSmol'>
      <h2 id='roomFeet'>
        = {volume}
        <p id='Cfoot'>Cubic Feet ft³</p>
      </h2>
    </div>
  </div>
);



export default RoomWireframe;
